export default "/* ==================== CSS Structure ====================\n1. COMMON STYLES\n\t- Reset Styles\n\t- Common Classes\n\t- Pre Loader Styles\n\n2. HTML ELEMENTS STYLE\n\t- Button Styles\n\n3. HEADER SECTION\n\t- Main Navigation Styles\n\t- Home section\n\n4. FEATURE SECTION\n5. SERVICE SECTION\n6. STEP SECTION\n7. Video SECTION\n\n8. SCREENSHOT SECTION\n\t- ScreenShots hover effect style\n\n9. TESTIMONIAL SECTION\n10. PRICING SECTION\n\t- Folding Effect\n\n11. SUBSCIBE SECTION\n12. DOWNLOAD SECTION\n13. CONTACT SECTION\n14. FOOTER SECTION\n========================================================== */\n\n\n/* --------------- Reset Styles --------------- */\n\n\nbody {\n  font-family: 'Open Sans', sans-serif;\n  font-size: 16px;\n  line-height: 28px;\n  font-weight: 300;\n  color: #666666;\n  overflow-x: hidden !important;\n  -webkit-font-smoothing: antialiased;\n}\n\n\na {\n  color: #cccccc;\n  text-decoration: none;\n}\n\n\na:hover, a:focus, .btn:focus {\n  text-decoration: none;\n  outline: none;\n  color: #6c6b6b\n}\n\n\ninput:focus, textarea:focus {\n  outline: 0;\n}\n\n\nh1, h2, h3, h4, h5, h6 {\n  font-family: 'Raleway', sans-serif;\n  text-transform:capitalize;\n  letter-spacing: 1px;\n}\n\n\nh1 {\n  font-size: 72px;\n  line-height: 82px;\n}\n\n\nh2 {\n  font-size: 48px;\n  line-height: 60px;\n}\n\n\nh3 {\n  font-size: 32px;\n  line-height: 45px;\n}\n\n\nh4 {\n  font-size: 20px;\n  line-height: 30px;\n}\n\n\nh5 {\n  font-size: 18px;\n  line-height: 25px;\n}\n\n\nimg {\n  max-width: 100%;\n  height: auto;\n}\n\n\nul, ol {\n  padding-left: 0 !important;\n}\n\n\nli {\n  list-style: none;\n}\n\n\n/* --------------- Common Classes --------------- */\n\n\n.center {\n  text-align: center;\n}\n\n\n.left {\n  text-align: left;\n}\n\n\n.justify {\n  text-align: justify;\n}\n\n\n.strong {\n  font-weight: 700;\n}\n\n\n.floatright {\n  float:right;\n}\n\n\n.floatleft {\n  float:left;\n}\n\n\n.floatnone {\n  float:none;\n}\n\n\n.fixed {\n  position:fixed;\n}\n\n\n.absolute {\n  position:absolute;\n}\n\n\n.relative {\n  position:relative;\n}\n\n\n.container {\n  position:relative;\n  z-index: 10;\n}\n\n\n.section-overlay {\n  position: absolute;\n  width: 100%;\n  height: 100%;\n}\n\n\n.animated {\n  visibility: hidden;\n}\n\n\n.visible {\n  visibility: visible;\n}\n\n\n.section-title {\n  margin-bottom: 48px;\n}\n\n\n.section-title h3, .section-title h4 {\n  color: #F4F4F4;\n  border-top: 1px solid;\n  border-bottom: 1px solid;\n  display: inline-block;\n  margin-top: 0;\n  padding: 8px 0;\n\n}\n\n\n.section-overlay {\n  background: rgba(0, 0, 0, .7) !important;\n  z-index: 5;\n}\n\n\n/* --------------- Pre Loader Styles --------------- */\n\n\n#preloader {\n  position:fixed;\n  top: 0;\n  left: 0;\n  background-color:#FFF;\n  width: 100%;\n  height: 100%;\n  z-index: 10050;\n}\n\n\n.loading-circle {\n  position: absolute;\n  left: 49%;\n  top: 49%;\n  height: 48px;\n  width: 48px;\n  border-radius: 50%;\n  border-top: 2px solid #7F8C8D;\n  border-right: 2px solid rgba(0,0,0, 0);\n  border-bottom: 2px solid #7F8C8D;\n  border-left: 2px solid rgba(0,0,0, 0);\n}\n\n\n/* =============== HTML element styles =============== */\n\n\n/* --------------- Button Styles --------------- */\n\n\n.button-line {\n  background: transparent;\n  display: inline-block;\n  font-weight: 400;\n  border-radius: 4px;\n  border: 1px solid;\n  margin: 10px;\n  overflow: visible;\n}\n\n\n.no-text .icon {\n  margin: 0;\n}\n\n\n.btn .icon {\n  margin-left: 13px;\n}\n\n\n.button-white {\n  border-color: #fff;\n  color: #fff;\n}\n\n\n.button-white:hover {\n  border-color: #fff;\n  color: #fff;\n}\n\n\n/* ================ Header Section ================ */\n\n\n.header-section {\n  position:relative;\n}\n\n\n/* --------------- Main Navigation Styles --------------- */\n\n\n.sticky-bar-wrap {\n  position:fixed;\n  width: 100%;\n  z-index: 1025;\n}\n\n\n.sticky-section {\n  width: 100%;\n  height: 80px;\n  z-index: 1025;\n  -webkit-transition: all .3s ease-in;\n  transition: all .3s ease-in;\n  background: rgba(0,0,0, .15);\n}\n\n\n.nav-bg {\n  background: rgba(0,0,0, .6);\n}\n\n\n.site-name {\n  color: #fff;\n  font-family: 'Bangers', cursive;\n  font-size: 24px;\n  padding: 8px 15px;\n  letter-spacing: 1px;\n}\n\n\n.site-name span {\n  color: #fff;\n  padding: 0 13px;\n  font-weight: 400;\n  height: 35px;\n  width: 35px;\n  display: inline-block;\n  margin-right: 3px;\n  font-size: 28px;\n  line-height: 35px;\n}\n\n\n.nav-hold {\n  left: 0;\n  right: 0;\n  top: 0;\n  padding-top: 13px;\n  z-index: 1025;\n}\n\n\nul.nav > li > a {\n  color:#fff;\n  font-size: 16px;\n  font-weight: 400;\n}\n\n\nul.nav > li > a:hover, ul.nav > li > a:focus {\n  background: none !important;\n}\n\n\nli.current a,\nul.nav > li > a:hover {\n  color:#ccc !important;\n}\n\n\n.navbar-toggle .icon-bar {\n  background: #ffffff;\n}\n\n\n/* --------------- Home section --------------- */\n\n\n.section-home {\n  position:relative;\n}\n\n\n.home {\n  min-height: 750px;\n}\n\n\n.well-come {\n  position:relative;\n  font-size: 50px;\n  line-height: 62px;\n  margin-top: 240px;\n  margin-bottom: 48px;\n  color:#fff !important;\n  font-weight: 700;\n  text-transform:uppercase;\n}\n\n\n.well-come:after {\n  position: absolute;\n  content: \"\";\n  width: 220px;\n  height: 1px;\n  background-color: #FFF;\n  bottom: -16px;\n  left: calc(50% - 110px);\n}\n\n\n.intro-message {\n  font-size: 20px;\n  line-height: 1.4;\n  margin-bottom: 48px;\n  color: #fff;\n}\n\n\n/* ================ Features Section ================ */\n\n\n#section-feature {\n  background-color: #f9f9f9;\n}\n\n\n.container.features {\n  padding-top: 132px;\n  padding-bottom: 190px;\n}\n\n\n.baraja-container {\n  width: 250px;\n  height: 400px;\n}\n\n\n.baraja-container .single-feature {\n  border: 1px solid #ccc;\n  border-radius: 4px;\n}\n\n\n.single-feature .feature-title {\n  margin-top: 18px;\n}\n\n\n.single-feature .feature-text {\n  line-height: 1.5;\n  padding: 0 10px;\n  margin-bottom: 16px;\n}\n\n\n.baraja-container .single-feature:hover {\n  -o-box-shadow: 0px 0px 10px rgba(110, 110, 110, 0.5);\n  box-shadow: 0px 0px 10px rgba(110, 110, 110, 0.5);\n}\n\n\n.single-feature .feature-image {\n\n}\n\n\n.features-control {\n  margin: auto;\n  margin-top: 55px;\n  width: 253px;\n  z-index: 1020;\n}\n\n\n.control-icon {\n  position: absolute;\n  top:0;\n  padding: 5px 15px;\n  font-size: 20px;\n  height: 46px;\n}\n\n\n.feature-link a{\n  color:#fff;\n  font-weight: 400;\n}\n\n\n.feature-link a i {\n  font-weight: 300;\n}\n\n\n.features-control #feature-prev {\n  left: 0px;\n}\n\n\n.features-control #feature-expand {\n  left: 60px;\n}\n\n\n.features-control #feature-close {\n  left: 120px;\n}\n\n\n.features-control #feature-next {\n  left: 180px;\n}\n\n\n/* ================ Services Section ================ */\n\n\n#section-services {\n  position:relative;\n  background-color: #333333;\n  color: #e5e5e5;\n}\n\n\n.container.services {\n  padding-top: 120px;\n  padding-bottom: 72px;\n}\n\n\n.service {\n  padding-bottom: 48px;\n}\n\n\n.service-icon {\n  float:left;\n  width: 30%;\n}\n\n\n.service-desc {\n  display:table;\n}\n\n\n.service-desc h4 {\n  margin-top: 0;\n}\n\n\n/* ================ Step-1,step-2,step-3 Sections ================ */\n\n\n.container.step {\n  padding-top: 120px;\n  padding-bottom: 120px;\n}\n\n\n.section-step {\n  background-color:#f9f9f9;\n}\n\n\n.step-even {\n  background-color:#333;\n  color:#e5e5e5;\n}\n\n\n.step-even .step-desc {\n  float:right;\n}\n\n\n.step-even .step-img {\n  float:right;\n}\n\n\n.step-video {\n  overflow:hidden;\n}\n\n\n.step-no {\n  width: 95px;\n  height: 95px;\n  border: 2px solid;\n  border-radius: 50%;\n}\n\n\n.step-no .no-inner {\n  font-size: 72px;\n  line-height: 86px;\n}\n\n\n.step-details .step-title {\n  margin-top: 0;\n  margin-bottom: 24px;\n}\n\n\n.step-details .step-description {\n  margin-bottom: 48px;\n}\n\n\n.step-details .sub-steps {\n  margin-left: 30px;\n}\n\n\n.step-details .sub-steps li {\n  margin-bottom: 24px;\n}\n\n\n.sub-steps .icon {\n  float:left;\n  border-top: 1px solid;\n  border-bottom: 1px solid;\n  border-left: 1px solid;\n  line-height: 40px;\n  padding: 0px 13px;\n  -webkit-transition: all .3s ease-in;\n  transition: all .3s ease-in;\n}\n\n\n.sub-steps .sub-text {\n  border: 1px solid;\n  height: 40px;\n  line-height: 40px;\n  display: table;\n  padding: 0 10px;\n}\n\n\n/* ================ Video Section ================ */\n\n\n#section-video {\n  position:relative;\n}\n\n\n.container.big-video {\n  padding-top: 96px;\n  padding-bottom: 120px;\n}\n\n\n.video-content iframe {\n  border: 0 !important;\n}\n\n\n/* ================ ScreenShots Section ================ */\n\n\n#section-screenshots {\n  background-color:#333333;\n  color:#e5e5e5;\n}\n\n\n.container.screenshots {\n  padding-top: 120px;\n  padding-bottom: 96px;\n}\n\n\n#portfolio-loader {\n  display:none;\n  position: relative;\n  min-height: 700px;\n}\n\n\n.porfolio-container {\n  position:relative;\n}\n\n\n.screenshot {\n  margin-bottom: 32px;\n  padding: 4px;\n  border: 1px solid #444;\n  border-radius: 4px;\n}\n\n\n.photo-box{\n  position:relative;\n  overflow:hidden;\n}\n\n\n.photo-box img {\n  width: 100%;\n  -webkit-transition: all 1.2s linear;\n  transition: all 1.2s linear\n}\n\n\n.photo-overlay {\n  -ms-filter: \"progid:DXImageTransform.Microsoft.Alpha(Opacity=0)\";\n  filter: alpha(opacity=0);\n  opacity: 0;\n  position: absolute;\n  top: 0;\n  left: 0;\n  width: 101%;\n  height: 100%;\n  background-color: rgba(0, 0, 0, 0.6);\n  -webkit-transition: all 0.5s ease-in;\n  transition: all 0.5s ease-in\n}\n\n\n.photo-overlay h4 {\n  display:inline-block;\n  color:#fff;\n  font-size: 16px;\n  font-weight: 700;\n  padding: 4px;\n  margin-top: 30px;\n}\n\n\n.photo-zoom:before {\n  -ms-filter: \"progid:DXImageTransform.Microsoft.Alpha(Opacity=0)\";\n  filter: alpha(opacity=0);\n  opacity: 0;\n  content: \"\";\n  border-style: none solid solid none;\n  border-width: 0 130px 130px 0;\n  right: 0;\n  bottom: 0;\n  overflow: hidden;\n  position: absolute;\n  -webkit-transform: rotate(-90deg) translatex(-40px) translatey(40px);\n  transform: rotate(-90deg) translatex(-40px) translatey(40px);\n  -webkit-transition: all 0.5s ease-in;\n  transition: all 0.5s ease-in\n}\n\n\n.photo-zoom a {\n  color: #fff;\n  -ms-filter: \"progid:DXImageTransform.Microsoft.Alpha(Opacity=0)\";\n  filter: alpha(opacity=0);\n  opacity: 0;\n  position: absolute;\n  bottom: 20px;\n  right: 20px;\n  -webkit-transition: all 0.5s ease-in;\n  transition: all 0.5s ease-in;\n  -webkit-transform: translatex(40px) translatey(40px);\n  transform: translatex(40px) translatey(40px)\n}\n\n\n.photo-zoom a:hover {\n  color:#ccc;\n}\n\n\n/* ------------- ScreenShots hover effect style ------------- */\n\n\n.screenshot:hover .photo-overlay {\n  -ms-filter: \"progid:DXImageTransform.Microsoft.Alpha(Opacity=100)\";\n  filter: alpha(opacity=100);\n  opacity: 1\n}\n\n\n.screenshot:hover .photo-box img {\n  -webkit-transform: scale(1.2);\n  transform: scale(1.2)\n}\n\n\n.screenshot:hover .photo-zoom:before {\n  -ms-filter: \"progid:DXImageTransform.Microsoft.Alpha(Opacity=100)\";\n  filter: alpha(opacity=100);\n  opacity: 1;\n  -webkit-transform: rotate(-90deg) translatex(0) translatey(0);\n  transform: rotate(-90deg) translatex(0) translatey(0)\n}\n\n\n.screenshot:hover .photo-zoom a {\n  -ms-filter: \"progid:DXImageTransform.Microsoft.Alpha(Opacity=100)\";\n  filter: alpha(opacity=100);\n  opacity: 1;\n  -webkit-transform: translatex(0) translatey(0);\n  transform: translatex(0) translatey(0)\n}\n\n\n/* ------------- Single Project ------------- */\n\n\n#portfolio-load {\n  display: none;\n  position:relative;\n  left: 105%;\n  min-height: 400px;\n}\n\n\n.backToProject {\n\n}\n\n\n.back-button {\n  display:none;\n  padding-top: 60px;\n}\n\n\n.project-details h5, .project-info h5 {\n  display:inline-block;\n  margin-top:0;\n  font-weight: 600;\n  border-bottom: 1px solid;\n  padding-bottom: 10px;\n}\n\n\n.project-details .details-list {}\n\n\n.project-details .details-list li {\n  color:#e5e5e5;\n  margin-bottom: 4px;\n}\n\n\n.project-details .details-list li i {\n  margin-right: 7px;\n}\n\n\n.project-details .details-list li .strong {\n  margin-right: 8px;\n}\n\n\n/* ================ Testimonials Section ================ */\n\n\n#section-testimonials {\n  color:#e5e5e5;\n}\n\n\n.testimonials-wrap {\n  position:relative;\n}\n\n\n.testimonials {\n  padding-top: 120px;\n  padding-bottom: 120px;\n}\n\n\n.testimonials .comment {\n  font-size: 18px;\n  margin-bottom: 48px;\n}\n\n\n.testimonial .happy-client {\n  display: table;\n  border-bottom: 1px solid;\n  max-width: 280px;\n  font-weight: 400;\n  margin: 0 auto 5px;\n}\n\n\n.testimonial .client-info {\n  font-size: 14px;\n}\n\n\n#bx-pager {\n  margin-top: 48px;\n}\n\n\n.client-photos .photo-hold {\n  display:inline-block;\n  width: 100px;\n  height: 100px;\n  padding: 10px;\n  margin-right: 20px;\n  border: 1px solid;\n  border-radius: 4px;\n  box-sizing: border-box;\n}\n\n\n.client-photos .photo-bg {\n  background: #000;\n  display: block;\n}\n\n\n.client-photos img {\n  opacity: .4;\n}\n\n\n.client-photos .active img {\n  opacity: 1;\n}\n\n\n/* ================ Pricing Section ================ */\n\n\n#section-pricing {\n  background: #f9f9f9;\n  color:#666;\n}\n\n\n.container.pricing {\n  padding-top: 120px;\n  padding-bottom: 120px;\n}\n\n\n.pricing .section-title h3 {\n  color:#666;\n}\n\n\n.single-pricing {\n  position: relative;\n  background: #FFFFFF;\n  border: 1px solid #d6d6d6;\n  border-radius: 4px;\n}\n\n\n.pricing-head {\n  overflow: hidden;\n  padding: 24px 0;\n}\n\n\n.best-pricing .pricing-head {\n  background: #333;\n}\n\n\n.best-pricing .price h3 {\n  color:#e5e5e5;\n}\n\n\n.pricing-heading {\n  margin-top: 0;\n  margin-bottom: 0;\n}\n\n\n.single-pricing .price h3 {\n  margin-top: 0;\n  margin-bottom: 0;\n  font-weight: 700;\n  line-height: 38px;\n}\n\n\n.single-pricing .price .dollar {\n  font-size: 20px;\n  vertical-align: super;\n  font-weight: normal;\n}\n\n\n.single-pricing .price .month {\n  font-size: 14px;\n  font-family: 'Lato', sans-serif;\n  font-style: italic;\n  font-weight: 300;\n  color:#959292;\n}\n\n\n.single-pricing .package-features {\n  margin-bottom: 15px;\n}\n\n\n.single-pricing .package-features li:first-child {\n  border-top: 1px solid #EEE;\n}\n\n\n.single-pricing .package-features li {\n  margin: 0px;\n  padding-bottom: 9px;\n  padding-top: 9px;\n  border-bottom: 1px solid #EEE;\n  color: #666;\n}\n\n\n.single-pricing .package-features li span {\n  margin-right: 8px;\n}\n\n\n.single-pricing .sign-up {\n  padding-bottom: 15px;\n}\n\n\n/* ================ Subscribe Section ================ */\n\n\n#section-subscribe {\n  position:relative;\n}\n\n\n.container.subscribe {\n  padding-top: 120px;\n  padding-bottom: 120px;\n}\n\n\n.subscription-success, .subscription-failed {\n  display:none;\n  color: #e5e5e5;\n}\n\n\n.subscribe h2 {\n  margin-bottom: 30px;\n}\n\n\n#subscription-form {\n  overflow: hidden;\n  width: 100%;\n}\n\n\n#subscription-form  .input-email {\n  display: block;\n  height: 55px;\n  padding-left: 20px;\n  width: 480px;\n  color: #F4F4F4;\n  background: transparent;\n  border-radius: 4px;\n  border: 1px solid #fff;\n  margin: auto;\n  margin-bottom: 14px;\n}\n\n\n/* ================ Download Section ================ */\n\n\n#section-download {\n  background-color: #333;\n}\n\n\n.container.download {\n  padding-top: 120px;\n  padding-bottom: 120px;\n}\n\n\n.download h2 {\n  margin-bottom: 30px;\n}\n\n\n.download-buttons {\n  clear:both;\n}\n\n\n.download-buttons a {\n  margin-top: 0;\n}\n\n\n/* ================ Contact Section ================ */\n\n\n#section-contact {\n  position:relative;\n}\n\n\n.contact {\n  padding-top: 120px;\n  padding-bottom: 120px;\n}\n\n\n.contact h2 {\n  margin-bottom: 30px;\n}\n\n\n.confirmation {\n  display:none;\n  clear: both;\n  color: #e5e5e5;\n}\n\n\n.confirmation p span {\n  margin-right: 8px;\n}\n\n\n.contact-form {\n  color:#f9f9f9;\n}\n\n\n.contact-form .input-field {\n  margin-bottom: 10px;\n  margin-top: 10px;\n  height: 50px;\n}\n\n\n.textarea-field, .input-field {\n  border: 1px solid #111;\n}\n\n\n.contact-form .form-item {\n  width: 100%;\n  padding: 5px 15px;\n  border-color:#ffffff;\n  border-radius: 4px;\n  background: transparent;\n  box-shadow: none;\n  -webkit-transition: all .3s ease-in;\n  transition: all .3s ease-in;\n}\n\n\n.contact-form .form-item::-webkit-input-placeholder {\n  color: #f9f9f9;\n}\n\n\n.contact-form .form-item:-moz-placeholder {\n  color: #f9f9f9;\n}\n\n\n.contact-form .form-item::-moz-placeholder {\n  color: #f9f9f9;\n}\n\n\n.contact-form .form-item:-ms-input-placeholder {\n  color: #f9f9f9;\n}\n\n\n.contact-form .subform {\n  margin-top: 20px;\n}\n\n\n.contact-form .form-item:hover, .contact-form .form-item:focus {\n  border-color:#ccc;\n  box-shadow: none;\n}\n\n\n/* ================ Footer Section ================ */\n\n\n#section-footer {\n  padding-top: 80px;\n  padding-bottom: 80px;\n  background: #333;\n  color:#e5e5e5;\n}\n\n\n.footer-title {\n  display: inline-block;\n  margin-bottom: 40px;\n  text-transform: none;\n}\n\n\n.social-icons ul li {\n  display: inline-block;\n}\n\n\n.social-icons ul li a{\n  margin-left: 10px;\n  font-size: 28px;\n  color: #9D9D9D;\n  font-weight: normal;\n  text-shadow: none;\n\n  -webkit-transition: all .5s ease-in;\n  transition: all .5s ease-in;\n}\n\n\n.copyright {\n  padding-top: 15px;\n}\n\n\n/* FB popUp */\n\n\n.fb-popup {\n  display: none;\n  position: fixed;\n  max-width: 460px;\n  background-color: #fff;\n  border: 1px solid #ccc;\n  padding: 25px;\n  top: 150px;\n  left: 0;\n  right: 0;\n  margin: auto;\n  z-index: 100000;\n}\n\n\n.fb-close {\n  position: absolute;\n  right: 10px;\n  top: 10px;\n  color: #B5B5B5;\n  cursor: pointer;\n}\n\n\n.fb-popup h5{\n  text-align: center;\n  text-transform: uppercase;\n  color: #4A4949;\n  font-size: 1em;\n  font-weight: 700;\n  margin-bottom: 30px;\n}\n\n\n/* ========== Responsive Styles ========== */\n\n\n/* --------- max width 991px -------- */\n\n\n@media only screen and (max-width: 991px) {\n  /* Main menu */\n  .navbar-nav > li > a {\n    font-size: 14px !important;\n  }\n\n  .step-no {\n    margin: 0 auto 60px;\n  }\n\n  /* steps */\n  .step-even .step-img, .step-even .step-details {\n    float: none;\n  }\n\n  .step-img, .step-details{\n    margin-bottom: 40px;\n  }\n\n  .step-img {\n    text-align:center;\n  }\n\n  .single-pricing {\n    margin-bottom: 60px;\n  }\n\n  .project-photo, .project-details {\n    margin-bottom: 60px;\n  }\n\n\n}\n\n\n/* --------- Styles for Screen width between 768px To 1023px -------- */\n\n\n@media only screen and (min-width: 768px) and (max-width: 1023px) {\n  /* Features */\n  .baraja-container {\n    width: 180px;\n    height: 275px;\n  }\n\n  .single-feature .feature-icon i {\n    font-size: 70px;\n  }\n\n  .single-feature .feature-title {\n    font-size: 18px;\n    line-height: 24px;\n  }\n\n  .single-feature .feature-text {\n    display:none;\n  }\n\n\n\n}\n\n\n/* --------- Styles for Screen width Smaller than 767px -------- */\n\n\n@media only screen and (max-width: 767px) {\n\n  /* Main menu */\n  .navbar-collapse {\n    margin-left: -16px;\n    margin-right: -16px;\n    background: #222;\n    overflow-y:scroll;\n    max-height: 200px;\n  }\n\n  .navbar-nav > li > a {\n    padding-left: 30px;\n  }\n\n\n  /* features */\n  .container.features {\n    padding-bottom: 120px;\n  }\n\n  .baraja-container {\n    width: 180px;\n    height: 250px;\n  }\n\n\n  .single-feature .feature-title {\n    font-size: 14px;\n    line-height: 20px;\n    padding: 0 5px;\n  }\n\n  .single-feature .feature-text {\n    display:none;\n  }\n\n  /* services */\n  .service {\n    margin-bottom: 20px;\n  }\n\n  .service .service-description {\n    margin: auto;\n    max-width: 480px;\n  }\n\n  /* Steps */\n  .step-even .step-img, .step-even .step-details {\n    float: none;\n  }\n\n  .step .step-img {\n    margin-top: 30px;\n  }\n\n  .sub-steps .sub-text {\n    line-height: 28px;\n  }\n\n  /* Screenshots */\n  .figure {\n    width: 33.333%;\n  }\n\n  .figure-large {\n    width: 66.6667%;\n  }\n\n  /* Pricing table */\n  .single-pricing-wrap {\n    margin-bottom: 40px;\n  }\n}\n\n\n/* --------- Styles for Screen width Smaller than 480px -------- */\n\n\n@media only screen and (max-width: 480px) {\n  /* Headings for mobile View */\n  h1 {\n    font-size: 36px;\n    line-height: 48px;\n  }\n\n  h2 {\n    font-size: 26px;\n    line-height: 34px;\n  }\n\n  h3 {\n    font-size: 24px;\n    line-height: 32px;\n  }\n\n\n  #section-feature {\n    padding-top: 140px;\n    padding-bottom: 40px;\n  }\n\n  .well-come {\n    margin-top: 150px;\n    font-size: 48px;\n  }\n\n  .features-control {\n    float: left;\n    height: 160px;\n    width: 42px;\n    bottom: 340px;\n  }\n\n  .features-control .control-icon {\n    position: relative;\n    left: 2px !important;\n    float: left;\n    margin-bottom: 8px;\n  }\n\n  /* Steps */\n  .step-details .step-description {\n    text-align:justify;\n  }\n\n  .sub-steps {\n    margin-left: 5%;\n  }\n\n\n  /* Sreenshots */\n  .screenshots .col-xs-6 {\n    width: 100% !important;\n  }\n\n  #subscription-form  .input-email {\n    width: 320px;\n  }\n}\n\n\n/* --------- Styles for Screen width Smaller than 360px -------- */\n\n\n@media only screen and (max-width: 360px) {\n  .client-photos .photo-hold {\n    width: 80px;\n    height: 80px;\n  }\n\n  /* Subscription Form */\n  #subscription-form  .input-email {\n    width: 250px;\n  }\n}\n\n\n\n\n\n\n/*# sourceMappingURL=data:application/json;base64, */"